@font-face{
  font-family: "Montserrat";
  src: "./components/Montserrat-Bold.ttf";
  font-weight: Bold;
}
@font-face{
  font-family: "Montserrat";
  src: "./components/Montserrat-Medium.ttf";
  font-weight: medium;
}
@font-face{
  font-family: "Montserrat";
  src: "./components/Montserrat-Light.ttf";
  font-weight: lighter;
}
@font-face{
  font-family: "Roboto";
  src: "./components/Home/Roboto-Bold.ttf";
  font-weight: Bold;
}
@font-face{
  font-family: "Roboto";
  src: "./components/Home/Roboto-Black.ttf";
  font-weight: 900;
}


*{
  margin: 0;
  padding: 0;
}
body{
  width: 100%;
  min-height: 100vh;
}


/*
########
PARTIE HEADER
########
*/

header{
  height: 80px;
  width: 100%;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  color: #653F49;
  box-shadow: 0px 2px 20px #00000030}

.marqueSign{
  padding-left: 120px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.nomMarque{
  font-size: 18px;
  font-family: 'Montserrat';
  font-weight: Bold;
}

.logoMaque{
  height: 40px;
  width: 60px;
}

.ulNav{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: row;
  padding-right: 273px;
}
li{
  padding: 0 0 0 72px;
  text-decoration: none;
  list-style: none;
  font-family: 'Montserrat';
  font-weight: Medium;
  font-size: 15px;
}
.navItem{
  text-decoration: none;
  color: #653F49;
}

.donate{
  color: white;
  background-color: #3FC2D9;
  border: none;
  width: 119px;
  height: 45px;
  font-size: 13px;
  border-radius: 4px;
  text-align: center;
  font-family: 'Montserrat';
  font-weight: Medium;
  cursor: pointer;
  outline: none;
}
.home{
  display: inline-block;
  position: relative;
}
.home:after{
  position: absolute;
  content: "";
  border-bottom: 2px solid #3FC2D9;
  width: 120%;
  transform: translateX(-50%);
  bottom: -10px;
  left: 50%;
}
/*
########
PARTIE PRESENTATION
########
*/
section{
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  left: 200px;
  width: 1200px;
  

}
.container{
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: 550px;

  
}
.presentation{
  width: 904px;
  height: 500px;
  padding-top: 100px;
}
.txtContainer{
  display: flex;
  flex-direction: column;
  position: relative;
  left: 30px;
}
.arrow{
  height: 18px;
  width: 18px;
  margin-left: 10px;
}
.title{
  padding-top: 40px;
  font-family: "Roboto";
  font-weight: Bold;
  color: #653F49;
  font-size: 40px;
}
.description{
  color: #653F49;
  padding-top: 30px;
  font-weight: 900;
  font-family: "Roboto";
  font-size: 15px;
  line-height: 24px;
}
.go{
  margin-top: 30px;
  height: 38px;
  width: 140px;
  color: white;
  font-size: 12px;
  background-color: #C06170;
  border-radius: 45px;
  justify-content: center;
  display: flex;
  align-items: center;
  outline: none;
  box-shadow: 0px 10px 20px #00000030;
  text-decoration: none;
}

/*
########
PARTIE Grille
########
*/


.grilleContainer{
  position: absolute;
  left: 0;
  width: 100%;
  top: 100%;
  display: flex;
  flex-direction: column;
}
.txtGrille{
  text-align: center;
  font-weight: 900;
  font-family: "Roboto";
  font-size: 50px;
  color: #653F49;
  padding-bottom: 100px;

}
.item{
  margin: 0 auto;
  height: 300px;
  box-shadow: 0px 4px 10px #00000030;
  transition: 400ms all ;
}
.item:hover{
  transform: scale(1.3);
}
.grille{
  display: grid;
  grid-template-columns: repeat(3, minmax(min-content, 300px)); /* cree autent de colone que besoin de 150px a 1fr */
  grid-template-rows: max-content;
  grid-gap: 70px;
  padding-bottom: 493px;
}

/*
########
PARTIE FOOTER
########
*/


.footerContainer{
  width: 100%;
  height: 301px;
  background-color: #4D4747;
  position: absolute;
  width: 100%;
  bottom: 0;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.footerLogo{
  padding-bottom: 150px;
  color: white;
}
.newsletter{
  display: flex;
  flex-direction: column;
}
label{
  color: white;
  opacity: 71%;
  font-family: "Montserrat";
  font-weight: medium;
  font-size: 14px;
}
input{
  width: 260px;
  height: 20px;
  margin-top: 10px;
}
.textFooter{
  margin-top: 10px;
  color: white;
  opacity: 71%;
  font-family: "Montserrat";
  font-weight: medium;
  font-size: 14px;
}
.contactList{
  margin-top: 5px;

  color: white;
  opacity: 71%;
  font-family: "Montserrat";
  font-weight: Lighter;
  font-size: 12px;
}
.lienContact{
  color: white;
  text-decoration: none;
  font-family: 'Montserrat';
  font-weight: Medium;
  font-size: 15px;
}
.contact{
  padding: 0 100px 0 100px
}

/*
########
PARTIE ERREUR
########
*/

.erreur{
  min-height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
  color: black;
  font-size: 30px;
}

/*
########
PARTIE ADMIN
########
*/
.grilleAdmin{
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.itemAdmin{
  padding: 30px 0 30px 0;
  height: 300px;
}
.formAdmin{
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.labelAdmin{
  padding-top: 100px;
  color: black;
  font-size: large;
}
.inputAdmin{
  width: 250px;
  height: 20px;
  padding: 5px;
}
.buttonAdmin{
  padding: 8px;
  width: 70px;
  border: none;
  background-color: #3FC2D9;
  color: wheat;
  border-radius: 5px;
  outline: none;
  margin-top: 20px;
}
.trash{
  height: 20px;
  cursor: pointer;
}
.btnGoogle{
  margin: 0 auto;
  margin-top: 100px;
}
.notAdmin{
  text-align: center;
  margin-top: 20px;
}
.logout{
  margin-left: 20px;
  padding: 8px;
  width: 70px;
  border: none;
  background-color: #C06170;
  color: wheat;
  border-radius: 5px;
  outline: none;
  margin-top: 20px;
}